<template>
	<div>
		<div class="logo-007" v-if="isShow&&!close">
			<a class="downbox" ref="downbox" :href="url" target="_blank" @click="download">
				<img id="bottomImg" :src="img" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
			</a>
			<div @click="closeBtn" class="btn"></div>
		</div>
	</div>
</template>
<script>
  import * as utils from '../../common/js/utils'
  import androidImg from '../../assets/images/downlink/android.png'
	export default {
		name: 'close',
		props:{
      showD:[Boolean,String],
      Banner:{
        type:Object,
        default:{
          iosShow:false,androidShow:false
        }
      }
    },
		data() {
			return {
        close:false,
        isYT:false,
        yt:{}
			}
		},
		computed:{
      img:function () {
        let img = ''
        let browser = utils.checkDevice()
        if(this.isYT){
          img = this.yt.icon
        }else{
          img = browser.versions.android?this.Banner.androidImg:this.Banner.iosImg
        }
        return img
      },
      url:function () {
        const browser = utils.checkDevice()
        //判断是否移动端
        let url = 'http://m.007kq.com/download.html'
        if(this.isYT){
          url = browser.versions.android?this.yt.androidUrl:this.yt.iosUrl
        }else {
          url =  browser.versions.android?this.Banner.androidUrl:this.Banner.iosUrl
        }
        return url
      },
      isShow:function () {
        let browser = utils.checkDevice();
        let isShow = false
        if(this.isYT){
          isShow = this.yt.status==1?false:true
        }else{
          isShow = browser.versions.android?this.Banner.androidShow:this.Banner.iosShow
        }
        return isShow
      }
    },
		created() {
      if(sessionStorage.getItem('downlink')=='false'){
        this.close = true
      }else{
        this.close = false
      }
      if(utils.getPara('ADTAG')&&utils.getPara('ADTAG')!=' '){
        this.getYT(utils.getPara('ADTAG'))
      }
		},
    methods:{
      download(){
        const browser = utils.checkDevice()
        //判断是否移动端
        if(browser.versions.android) {
          MtaH5.clickStat('clickEvent',{'phone':'android'})
        } else {
          MtaH5.clickStat('clickEvent',{'phone':'ios'})
        }
      },
      getYT(channel){
        console.log(channel)
        let toData = {
          url: 'ggw/getGgw',
          callback: (res) => {
            if(res.error_code === 1) {
              res.data.map((item,index)=>{
                if(channel==item.channel){
                  this.isYT = true
                  this.yt = item
                }
              })
            }
          },
          error: () => {
            console.log('网络错误')
          }
        }
        utils.cpost(this, toData);
      },
      closeBtn(){
        this.close = true
        sessionStorage.setItem('downlink','false')
        const browser = utils.checkDevice()
        //判断是否移动端
        if(browser.versions.android) {
          MtaH5.clickStat('clickEvent',{'closeban':'android'})
        } else {
          MtaH5.clickStat('clickEvent',{'closeban':'ios'})
        }
      }
    }
	}
</script>
<style scoped>
	.logo-007 {
		display: block;
		width: 100%;
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: 9999;
	}

	.logo-007 .downbox {
		display: block;
    cursor: pointer;
    position: relative;
	}

	.logo-007 img {
		width: 100%;
		display: block;
	}

	.btn {
		position: absolute;
		z-index: 100;
		width: 7%;
		height: 50%;
		background: rgba(0, 0, 0, 0);
		right: 0;
		top: 0;
	}
</style>



