<template>
  <div class="contact" v-if="show">
    <a v-if="url" :href="url" class="contact-a"></a>
    <div class="box">
      <span>{{Banner.content?Banner.content.split('\n')[0]:'球迷微信群：993760177 看球聊球'}}</span>
      <span>{{Banner.content?Banner.content.split('\n')[1]:'微信公众号：a007kanqiu 高清 英超 西甲 NBA'}}</span>
    </div>
    <!--<img src="../../assets/images/ban/hban.png" alt="007kq,007看球,007体育直播,007直播" />-->
  </div>
</template>
<script>
  import * as utils from '../../common/js/utils'
  export default{
    name:'contact',
    props:{
      Banner:{
        type:Object,
        default:{
          iosShow:false,androidShow:false
        }
      }
    },
    data(){
      return{

      }
    },
    computed:{
      show:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.Banner.androidShow
        }else{
          return this.Banner.iosShow
        }
      },
      url:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.Banner.androidUrl
        }else{
          return this.Banner.iosUrl
        }
      }
    },
  }
</script>
<style scoped>
  .contact{
    width: 100%;
    text-align: center;
    font-size:.30rem;
    font-weight: bold;
    color:#fff;
    background: #1e1e2c;
    line-height: .4rem;
    min-height: 1.67rem;
    position: relative;
    box-sizing: border-box;
  }
  .contact .contact-a{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    z-index: 10;
  }
  .contact .box{
    position: absolute;
    top:50%;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
    padding: .2rem;
    box-sizing: border-box;
  }
  .contact span{
    display: block;
  }
</style>



