import Vue from 'vue'
import vueResource from 'vue-resource'
import VueLazyload from 'vue-lazyload'
import App from './App'
import '../../common/css/reset.css'
import '../../common/css/mixin.styl'


Vue.use(vueResource)
Vue.use(VueLazyload)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: {App}
})



