<style lang="less">
  @import "./css/order.less";
</style>
<script>
  import * as utils from '../../common/js/utils'
  import contact from '../components/contact.vue'
  import downlink from '../components/downlink.vue'
  export default{
    name:'order',
    components:{contact,downlink},
    data(){
      return{
        isqd:'',
        bannerArr:{
          downlink:{iosShow:false,androidShow:false},
          contact:{iosShow:false,androidShow:false},
          appPop:{iosShow:false,androidShow:false},
          listBan:{iosShow:false,androidShow:false},
        },
        mid:'',
        info:{}
      }
    },
    created(){
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):'';
      document.getElementById('loading').style.display = 'none';
      this.getBannerData();
      this.mid = utils.getPara('mid')
      this.initData()
    },
    methods:{
      getBannerData(){
        let toData = {
          url:'ggw/mobile/getAll',
          callback:(res)=>{
            /*console.log(res.data)*/
            if(res.error_code==1){
              let list = res.data
              //  type广告位置类型 0：底层广告 1：列表广告 2：悬浮窗广告 3：文字广告
              list.map((item,index)=>{
                switch (item.type){
                  case 0:
                    this.bannerArr.downlink = item
                    break;
                  case 1:
                    this.bannerArr.listBan = item
                    break;
                  case 2:
                    this.bannerArr.appPop = item
                  case 3:
                    this.bannerArr.contact = item
                    break;
                }
              })
            }
          }
        }
        utils.cpost(this,toData)
      },
      initData(){
        let toData = {
          url:'worldcup/matchInf',
          postData:{mid:this.mid},
          callback:(res)=>{
            console.log(res)
            if(res.error_code==1){
              this.info = res.data
            }else{
              console.error(res)
            }
          }
        }
        utils.cpost(this,toData)
      },
      hasLogo(url){
        if(url){
          if(url.indexOf('http')>=0){
            return true
          }else{
            return false
          }
        }else{
          return false
        }
      },
      getTime(time){
        if(time){
          time = time.split('.')[0]
          time = time.replace(/-/g,'/')
          let date = new Date(time)

          let M = date.getMonth() + 1
          let D = date.getDate()
          let h = date.getHours()
          let m = date.getMinutes()
          M = (M>=10?M:'0'+M)
          D = (D>=10?D:'0'+D)
          h = (h>=10?h:'0'+h)
          m = (m>=10?m:'0'+m)
          let str = M + '月' + D + '日 ' + h + ':' + m
          return str
        }else{
          return time
        }

      }
    }
  }
</script>



